@import url(https://fonts.googleapis.com/css?family=Acme|Luckiest+Guy|Oswald);
@import url(https://fonts.googleapis.com/css?family=Raleway);
#pulse {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  background-color: #ff5252;
  border-radius: 50%;
  font-size: 20px;
  font-family: "Raleway";
  text-transform: uppercase;
  -webkit-animation: pulse 1.5s linear infinite;
          animation: pulse 1.5s linear infinite; }

#pulse-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20%; }

#pulse:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused; }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    box-shadow: 0px 0px 0px 0px #ff5252; }
  40% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    box-shadow: 0px 0px 0px 4px #ff6363; }
  60% {
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0px 0px 0px 6px #ff7474; }
  80% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0px 0px 0px 8px #ff8585; }
  80% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    box-shadow: 0px 0px 0px 9px #ffb9b9; }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0px 0px 0px 14px #ffb3b3; } }

@keyframes pulse {
  0% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    box-shadow: 0px 0px 0px 0px #ff5252; }
  40% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    box-shadow: 0px 0px 0px 4px #ff6363; }
  60% {
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0px 0px 0px 6px #ff7474; }
  80% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0px 0px 0px 8px #ff8585; }
  80% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    box-shadow: 0px 0px 0px 9px #ffb9b9; }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0px 0px 0px 14px #ffb3b3; } }

#poly {
  visibility: hidden;
  -webkit-clip-path: polygon(0 36%, 16% 14%, 35% 0, 85% 0%, 100% 0, 100% 15%, 100% 85%, 100% 100%, 85% 100%, 15% 100%, 0 100%, 0% 85%);
  clip-path: polygon(0 36%, 16% 14%, 35% 0, 85% 0%, 100% 0, 100% 15%, 100% 85%, 100% 100%, 85% 100%, 15% 100%, 0 100%, 0% 85%);
  height: 97vh;
  width: 40%;
  background-color: #ddd;
  position: absolute;
  top: 6vh;
  right: 0;
  display: flex;
  align-items: flex-end;
  -webkit-animation: move 1.5s ease-in-out 0.5s;
          animation: move 1.5s ease-in-out 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: paused;
          animation-play-state: paused; }

@-webkit-keyframes move {
  0% {
    opacity: 0;
    visibility: visible;
    -webkit-transform: translateX(500px);
            transform: translateX(500px); }
  50% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px); }
  55% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px); }
  100% {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateX(0px);
            transform: translateX(0px); } }

@keyframes move {
  0% {
    opacity: 0;
    visibility: visible;
    -webkit-transform: translateX(500px);
            transform: translateX(500px); }
  50% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px); }
  55% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px); }
  100% {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateX(0px);
            transform: translateX(0px); } }

@media (max-width: 499px) {
  img[alt="programmer"] {
    width: 100%; } }

.artgallery {
  display: flex;
  flex-wrap: wrap; }

.painting {
  /*
  default value
  flex-grow: 0
  flex-shrink:1
  flex-basis:1 */
  width: 29%;
  height: 80vh;
  border-radius: 2.5%;
  margin: 3%;
  flex-grow: 1;
  flex-shrink: 1;
  /*by default */ }

@media (max-width: 499px) {
  .painting {
    width: 90%; } }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

body {
  background-color: #white; }

.posts {
  display: flex;
  flex-wrap: wrap; }

.post-category, .post-date {
  color: #738a94;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 80%; }

.post-date {
  float: right; }

.post-category {
  float: left; }

.post-body {
  padding: 3%; }

.post-title {
  font-family: Oswald;
  clear: both; }

.post {
  /*
  default value
  flex-grow: 0
  flex-shrink:1
  flex-basis:1 */
  background-color: white;
  width: 27.33%;
  border-radius: 2.5%;
  margin: 3%;
  flex-grow: 1;
  flex-shrink: 1;
  /*by default */ }

.post-content {
  font-family: 'Open Sans Condensed'; }

.post-image {
  width: 100%;
  border-top-left-radius: 2.5%;
  border-top-right-radius: 2.5%; }

.post a {
  text-decoration: none;
  color: black; }

.post:hover {
  -webkit-animation-name: scale;
          animation-name: scale;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out; }

@-webkit-keyframes scale {
  from {
    -webkit-transform: scale(1);
            transform: scale(1); }
  to {
    -webkit-transform: scale(1.01);
            transform: scale(1.01); } }

@keyframes scale {
  from {
    -webkit-transform: scale(1);
            transform: scale(1); }
  to {
    -webkit-transform: scale(1.01);
            transform: scale(1.01); } }

header {
  background-image: -webkit-radial-gradient(50% 50%, ellipse closest-side, #393e44, #36383b 25%, #343233);
  background-image: radial-gradient(ellipse closest-side at 50% 50%, #393e44, #36383b 25%, #343233);
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

header h1 {
  font-size: 100px;
  color: white;
  font-family: 'Luckiest Guy'; }

header h2 {
  color: #738a94;
  font-family: 'Acme'; }

.logo {
  position: relative;
  left: 30%;
  display: flex;
  width: 30%;
  flex-direction: row;
  color: #ddd;
  justify-content: space-between; }

@media (max-width: 499px) {
  .post {
    width: 94%; }
  header h2 {
    display: none; }
  header {
    height: 250px; }
  .logo {
    display: none; } }

nav {
  width: 100%;
  height: 6vh;
  background-color: #202021; }
  nav ul {
    font-family: 'Raleway', sans-serif;
    font-size: 15px;
    width: 55%;
    padding-top: 0.5%;
    list-style-type: none;
    display: flex;
    justify-content: space-around;
    text-decoration: none; }

.link-item {
  color: white;
  font-size: 100%; }

.link-item:hover {
  color: #ffd152; }

@media only screen and (max-width: 499px) {
  nav {
    width: 100%;
    height: 8vh; }
    nav ul {
      justify-content: space-around;
      width: 100%;
      height: 100%; }
  #pulse-container {
    visibility: hidden; } }

